@charset "utf-8";

/* --------------- ProductInput.js --------------- */
.input_item{
  width: 65%;
  margin: 2rem auto;
  padding: 20px;
  background: var(--beige_color);
  border-radius: 7px;
}
/* label+input 묶은 박스 */
.input_item p,
.edit_mode p
{
  margin-bottom: 0.7rem;
  display: flex; flex-direction: column;
  align-items: flex-start;
}
/* label 공통 서식 */
.input_item p > label,
.edit_mode p > label
{
  padding: 0.3rem;
  color: var(--base_color);
  font-size: 14px;
  font-weight: 700;
}
/* input 박스 공통 서식 */
.input_item p > input,
.edit_mode p > input
{
  width: 100%;
  border: 1px solid var(--base_color); border-radius: 5px;
  background: #fff;
  padding: 0.8rem 0.5rem;
  box-sizing: border-box; 
}

/* 내용추가버튼 추가서식 */
.input_item p > input[type='button']{
  margin-top: 1rem;
  color: var(--beige_color);
  background: var(--base_color);
}
.submit_btn:hover{
  text-decoration: underline;
  font-weight: 700;
}

/* --------------- Product.js --------------- */
.product_list{
  display: flex; flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  padding: 5rem; gap: 20px;
  margin: 0 auto;
}
.product_list li{
  width: 35%;
  border: 1px solid #ddd;
  border-radius: 7px;
  padding: 10px;
  text-align: left;
  box-sizing: border-box;
  overflow: hidden;
  background: var(--beige_color);
}
/* 상품 이미지 */
.img_box{width: 100%;  overflow: hidden;}
.img_box img{
  width: 100%;
  height: auto; 
  display: block;
  margin: 0 auto;
  transition: 0.5s;
}
.img_box img:hover{
  transform: scale(1.1);
  filter: brightness(1.15);
}
/* 상품 글자 */
.product_list li p{padding: 0.5rem;}
.p_num, .price{
  color: #bb5030;
  font-size: 1rem;
  font-weight: bolder;
  padding-bottom: 0.5rem;
}
.product_list li p:nth-child(3){
  font-size: 16px;
  padding-top: 1rem;
  font-weight: 500;

  display: -webkit-box; -webkit-box-orient: vertical; -webkit-line-clamp: 2; /*옛날 친구들이나 특정 브라우저를 위한 별명*/
  line-clamp: 2;  /*모든 브라우저가 다 알아듣는 표준 이름 추가*/
  overflow: hidden; text-overflow: ellipsis;
}
/* 수정하기,삭제하기 버튼 */
.btn_wrap{
  display: flex;
  width: 100%; gap: 0.8rem;  
  margin-top: 1rem;
}
.btn_wrap p{
  flex: 1;
  padding: 0; margin: 0;
}
.btn{
  width: 100%;
  border: none; border-radius: 5px;
  padding: 0.8rem 0.5rem;
  box-sizing: border-box; 
  
  color: var(--beige_color);
  background: var(--base_color);
}
.edit_btn, .save_btn{background: #EF8C01 !important;}
.cancel_btn{ 
  background: var(--base_color) !important; 
  color: var(--beige_color) !important;
}
.btn:hover{
  text-decoration: underline;
  font-weight: 700;
  cursor: pointer;
}

